* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: Commissioner, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #1F1F1F;
  background-color: #F6F6F6;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #BD2F2F;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #BD2F2F;
}
body::-webkit-scrollbar-track {
  background: #1F1F1F;
  border-radius: 0;
}
body {
  scrollbar-face-color: #BD2F2F;
  scrollbar-track-color: #1F1F1F;
}
body.overflow {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1430px;
  padding: 0 15px;
}

.header {
