.main-page {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.main-catalog {
  padding-top: 80px;
}
.main-catalog__title {
  color: #fff;
  font-weight: 400;
  font-size: 48px;
  line-height: 62px;
  text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.35);
  font-family: Esqadero, sans-serif;
}
.main-catalog__content {
  margin-top: 80px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  position: relative;
}
.main-catalog__content:after {
  content: "";
  display: block;
  width: 250px;
  height: 250px;
  background-image: url(../images/main/logos/logo.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
  top: calc(50% - 288px);
  left: calc(50% - 128px);
}
.main-catalog__content__item {
  min-width: 446px;
  margin: 0 20% 70px 0;
  position: relative;
  z-index: 1;
}
.main-catalog__content__item:before {
  content: "";
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
}
.main-catalog__content__item:after {
  content: "";
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: 0;
  right: 0;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
}
.main-catalog__content__item:nth-of-type(2n) {
  margin-right: 0;
  margin-left: auto;
}
.main-catalog__content__item .mcatalog-wrapper {
  padding: 50px 50px 55px;
  position: relative;
}
.main-catalog__content__item .mcatalog-wrapper:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  background-color: rgba(0, 0, 0, 0.22);
  opacity: 0;
  transition: width 0.3s, height 0.3s, opacity 0.3s;
  position: absolute;
  top: 0;
  left: 0;
}
.main-catalog__content__item .mcatalog-title {
  color: #fff;
  font-weight: 600;
  font-size: 24px;
  line-height: 41px;
  text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.15);
  text-transform: uppercase;
  transition: color 0.3s, text-shadow 0.3s;
}
.main-catalog__content__item .mcatalog-list {
  margin: 20px 0 0 0;
  padding: 0;
  position: relative;
}
.main-catalog__content__item .mcatalog-list__item {
  display: block;
  width: 100%;
  margin-top: 12px;
}
.main-catalog__content__item .mcatalog-list__item:nth-of-type(1) {
  margin-top: 0;
}
.main-catalog__content__item .mcatalog-list__item.more-link {
  margin-top: 22px;
}
.main-catalog__content__item .mcatalog-list__item.more-link .mcatalog-list__link {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 16px;
  line-height: 20px;
}
.main-catalog__content__item .mcatalog-list__item.more-link .mcatalog-list__link:after {
  content: "\e80a";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  margin: 0 0 0 16px;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  font-size: 7px;
  line-height: 10px;
  transition: transform 0.3s;
}
.main-catalog__content__item .mcatalog-list__item.more-link .mcatalog-list__link:hover:after {
  transform: translateX(5px);
}
.main-catalog__content__item .mcatalog-list__link {
  display: inline-block;
  color: #fff;
  font-size: 18px;
  line-height: 24px;
  transition: color 0.3s;
}
.main-catalog__content__item .mcatalog-list__link:hover {
  color: #BD2F2F;
}
.main-catalog__content__item:hover .mcatalog-wrapper:before {
  width: 100%;
  height: 100%;
  opacity: 1;
}
.main-catalog__content__item:hover .mcatalog-title {
  color: #BD2F2F;
  text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.15);
}
.main-catalog__content__scroll {
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 130px;
}
.main-catalog__content__scroll svg {
  display: block;
  margin: 0 auto;
  position: relative;
}
.main-catalog__content__scroll:after {
  content: "\e80a";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: block;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  position: absolute;
  bottom: -50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  transform: rotate(90deg);
