@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

.main-page
  background-repeat: no-repeat
  background-position: center
  background-size: cover
.main-catalog
  padding-top: 80px
  &__title
    color: #fff
    font-weight: 400
    font-size: 48px
    line-height: 62px
    text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.35)
    font-family: Esqadero, sans-serif
  &__content
    margin-top: 80px
    display: flex
    flex-wrap: wrap
    align-items: flex-start
    justify-content: space-between
    position: relative
    &:after
      content: ''
      display: block
      width: 250px
      height: 250px
      background-image: url(../images/main/logos/logo.png)
      background-repeat: no-repeat
      background-position: center
      background-size: contain
      position: absolute
      top: calc(50% - 288px)
      left: calc(50% - 128px)
    &__item
      min-width: 446px
      margin: 0 20% 70px 0
      position: relative
      z-index: 1
      &:before
        content: ''
        display: block
        width: 50px
        height: 50px
        position: absolute
        top: 0
        left: 0
        border-top: 1px solid #fff
        border-left: 1px solid #fff
      &:after
        content: ''
        display: block
        width: 50px
        height: 50px
        position: absolute
        bottom: 0
        right: 0
        border-bottom: 1px solid #fff
        border-right: 1px solid #fff
      &:nth-of-type(2n)
        margin-right: 0
        margin-left: auto
      .mcatalog-wrapper
        padding: 50px 50px 55px
        position: relative
        &:before
          content: ''
          display: block
          width: 0
          height: 0
          background-color: rgba(#000, .22)
          opacity: 0
          transition: width .3s, height .3s, opacity .3s
          position: absolute
          top: 0
          left: 0
      .mcatalog-title
        color: #fff
        font-weight: 600
        font-size: 24px
        line-height: 41px
        text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.15)
        text-transform: uppercase
        transition: color .3s, text-shadow .3s
      .mcatalog-list
        margin: 20px 0 0 0
        padding: 0
        position: relative
        &__item
          display: block
          width: 100%
          margin-top: 12px
          &:nth-of-type(1)
            margin-top: 0
          &.more-link
            margin-top: 22px
            .mcatalog-list__link
              position: relative
              display: inline-flex
              align-items: center
              justify-content: flex-start
              font-size: 16px
              line-height: 20px
              &:after
                content: '\e80a'
                font-family: Fontello
                font-style: normal
                font-weight: normal
                speak: never
                display: inline-block
                margin: 0 0 0 16px
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                font-size: 7px
                line-height: 10px
                transition: transform .3s
              &:hover
                &:after
                  transform: translateX(5px)
        &__link
          display: inline-block
          color: #fff
          font-size: 18px
          line-height: 24px
          transition: color .3s
          &:hover
            color: $red-color
      &:hover
        .mcatalog-wrapper
          &:before
            width: 100%
            height: 100%
            opacity: 1
        .mcatalog-title
          color: $red-color
          text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.15)
    &__scroll
      display: block
      width: 100%
      position: absolute
      left: 0
      bottom: 130px
      svg
        display: block
        margin: 0 auto
        position: relative
      &:after
        content: '\e80a'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        position: absolute
        bottom: -50px
        left: 0
        right: 0
        margin: 0 auto
        transform: rotate(90deg)
        color: #fff
        font-size: 7px
        animation: scroll-arrow infinite 2s
.main-about
  margin-top: 210px
  &__title
    color: #fff
  &__content
    display: flex
    flex-wrap: wrap
    align-items: stretch
    justify-content: flex-start
    color: #fff
    &__left-side
      flex: 0 0 calc(100% - 600px)
      max-width: calc(100% - 600px)
      width: 100%
      margin-right: 34px
      .descr-block
        margin-top: 17px
        line-height: 27px
        padding-right: 125px
        p
          margin-bottom: 27px
          padding-right: 165px
          &:nth-of-type(1)
            padding-right: 0
          &:last-child
            margin-bottom: 0
      .more-info
        margin-top: 30px
      .mperson-block
        margin-top: 87px
        display: flex
        flex-wrap: wrap
        align-items: stretch
        justify-content: flex-start
        .content-block
          background: $black-gradient
          padding: 25px
          max-width: 400px
          margin-bottom: 87px
          .person
            padding-left: 73px
            position: relative
            &:before
              content: '\e812'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              position: absolute
              top: 12px
              left: 0
              color: $red-color
              font-size: 49px
              line-height: 49px
            .person-name
              font-weight: 500
              font-size: 24px
              line-height: 41px
            .person-who
              font-size: 16px
              line-height: 27px
          blockquote
            display: block
            margin-top: 29px
            line-height: 27px
        .image-block
          flex: 0 0 auto
          max-width: calc(100% - 400px)
          width: 100%
          position: relative
          .person
            display: block
            width: auto
            max-width: 100%
            position: absolute
            bottom: 0
            right: 10px

    &__right-side
      flex: 0 0 566px
      max-width: 566px
      width: 100%
      padding-top: 2px
      .about-image
        display: block
        width: auto
        max-width: 100%
        margin: 0 auto
      .descr-block
        margin-top: 30px
        h3
          font-weight: 500
          font-size: 24px
          line-height: 41px
          display: block
          margin-bottom: 10px
        p
          font-size: 16px
          line-height: 27px
          b
            font-weight: 600



