.header
  position: absolute
  top: 0
  left: 0
  width: 100%
  &__content
    &__top
      display: flex
      align-items: center
      justify-content: space-between
      flex-wrap: wrap
      padding: 34px 0
      address
        display: inline-block
        position: relative
        padding-left: 30px
        font-style: normal
        font-weight: 400
        &:before
          content: '\e80d'
          font-family: Fontello
          font-size: 18px
          line-height: 20px
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $red-color
          position: absolute
          top: 0
          left: 0
      .phone-links
        margin: 0 auto
        .phone-link
          display: inline-block
          color: $dark-color
          position: relative
          padding-left: 30px
          margin: 0 15px
          &:before
            content: '\e810'
            font-family: Fontello
            font-size: 18px
            line-height: 20px
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            color: $red-color
            position: absolute
            top: 0
            left: 0
      .mail-link
        display: inline-block
        color: $dark-color
        position: relative
        padding-left: 38px
        &:after
          content: ''
          display: block
          width: calc(100% - 38px)
          height: 1px
          border-bottom: 1px dashed rgba($dark-color, .5)
          position: absolute
          right: 0
          bottom: -2px
          transition: border-color .3s
        &:before
          content: '\e80c'
          font-family: Fontello
          font-size: 18px
          line-height: 20px
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $red-color
          position: absolute
          top: 0
          left: 0
        &:hover
          &:after
            border-color: $dark-color
    &__center
      display: flex
      align-items: center
      justify-content: flex-start
      flex-wrap: wrap
      .logo-link
        display: inline-flex
        align-items: center
        justify-content: flex-start
        flex-wrap: nowrap
        color: $dark-color
        .logo-image
          display: block
          width: auto
          max-width: 95px
          margin-left: 4px
          margin-right: -5px
        .logo-descr
          display: inline-flex
          flex-direction: column
          align-items: flex-start
          justify-content: flex-start
          margin-left: 21px
          padding: 27px 0 0 16px
          position: relative
          &:before
            content: ''
            display: block
            width: 1px
            height: 32px
            position: absolute
            top: 32px
            left: 0
            background-color: $dark-color
          span
            &:nth-of-type(2)
              margin-top: 7px
      .search-form
        margin-left: 50px
        position: relative
        flex: 1 1 auto
        max-width: 489px
        .input-group
          display: flex
          flex-wrap: nowrap
          align-items: stretch
          justify-content: flex-start
          border: 1px solid rgba($dark-color, .5)
          input
            flex: 0 0 calc(489px - 67px)
            max-width: calc(489px - 67px)
            width: 100%
            background: transparent
            color: $dark-color
            border: none
            padding: 23px 20px
            @include input-placeholder
              color: rgba($dark-color, .5)
          button
            display: inline-flex
            align-items: center
            justify-content: center
            flex: 0 0 67px
            width: 67px
            color: $red-color
            background: transparent
            border: none
            font-size: 20px
            line-height: 20px
            transition: color .3s
            &:hover
              color: $red-color
      .backcall-btn
        flex: 1 1 auto
        width: auto
        max-width: 234px
        color: #fff
        text-align: center
        padding: 23px
        background: $red-gradient
        margin-left: 50px
        &:hover
          background: $red-color
      .mini-cart
        flex: 0 0 auto
        width: auto
        margin-left: auto
        padding: 0 15px
        .cart-link
          display: flex
          align-items: center
          justify-content: flex-start
          flex-wrap: nowrap
          color: $dark-color
          .cart-icon
            flex: 0 0 40px
            width: 100%
            max-width: 40px
            display: flex
            align-items: center
            justify-content: center
            color: $red-color
            font-size: 25px
            line-height: 25px
            margin-right: 12px
          .cart-info
            flex: 0 0 calc(100% - 40px)
            max-width: calc(100% - 40px)
            span
              display: block
              width: 100%
              white-space: nowrap
              &.count
                font-size: 14px
                line-height: 24px
                color: rgba($dark-color, .5)
              &.price-result
                font-size: 18px
                line-height: 30px

    &__menu
      background: $black-gradient
      margin-top: 33px
      position: sticky // tmp
      top: 0
      left: 0
      .main-menu
        &__list
          display: flex
          flex-wrap: wrap
          align-items: flex-start
          justify-content: space-between
          width: 100%
        &__item
          flex: 0 0 auto
          width: auto
          &.submenu-item
            position: relative
            .main-menu__link
              position: relative
              padding-right: 25px
              &:after
                content: '\e80e'
                font-family: Fontello
                font-style: normal
                font-weight: normal
                speak: never
                display: block
                margin: 0
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                position: absolute
                top: calc(50% - 5px)
                right: 0
                width: 10px
                height: 10px
                font-size: 7px
                line-height: 10px
            &.catalog-item
              .main-menu__link
                padding-right: 26px
                &:after
                  content: '\e804'
                  font-size: 12px
                  line-height: 12px
                  width: 12px
                  height: 12px
                  top: calc(50% - 5px)
            .submenu__list
              position: absolute
              top: 100%
              left: 0
              width: 100%
        &__link
          display: inline-block
          width: auto
          color: #fff
          font-size: 18px
          line-height: 20px
          padding: 20px 0
          transition: color .3s
          &:after
            transition: color .3s
            color: $red-color
          &:hover
            color: $red-color
            &:after
              color: #fff
  &.main-header
    .header__content
      &__top
        address
          color: #fff
        .phone-links
          .phone-link
            color: #fff
        .mail-link
          color: #fff
          &:after
            border-bottom: 1px dashed rgba($dark-color, .5)
          &:hover
            &:after
              border-color: #fff
      &__center
        .logo-link
          color: #fff
          .logo-descr
            color: #fff
            &:before
              background-color: #fff
        .search-form
          .input-group
            border: none
            input
              background: $black-gradient
              color: #fff
              @include input-placeholder
                color: rgba(#fff, .5)
            button
              color: rgba(#fff, .5)
              background: $black-gradient
              &:hover
                color: #fff
        .mini-cart
          .cart-link
            color: #fff
            .cart-info
              span
                &.count
                  color: #fff





  & + main, & + .main-page
    padding-top: 255px








